.page-login {
  height: 100%;
  background: url(/system-antd3.0/static/media/bg.10c85f42.jpeg) no-repeat center;
  background-size: cover;
  padding: 80px 0 10px;
  overflow: auto;
}
.page-login .form-box {
  width: 30%;
  min-width: 340px;
  max-width: 430px;
  margin: 0 auto;
  padding: 40px 20px 30px;
  background-color: rgba(0, 0, 0, 0.02);
  box-shadow: 0 0 20px #ccc;
  border-radius: 4px;
}
.page-login .form-box .box-header {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 40px;
}
.page-login .form-box .ant-tabs-bar {
  border-bottom: 4px solid #fff;
  text-align: center;
}
.page-login .form-box .ant-tabs-bar .ant-tabs-nav-container {
  margin-bottom: -4px;
}
.page-login .form-box .ant-tabs-bar .ant-tabs-nav-container .ant-tabs-ink-bar {
  height: 4px;
}
.page-login .form-box .login-form {
  padding: 40px 10px 0;
}
.page-login .form-box .login-form .ant-input,
.page-login .form-box .login-form .ant-btn {
  font-size: 12px;
}
.page-login .form-box .login-form .login-btn {
  margin-top: 20px;
}
.page-login .form-box .login-form .forget-box {
  font-size: 12px;
  text-align: center;
}
.page-login .form-box .login-form .qrcode-box {
  height: 200px;
  background: url(/system-antd3.0/static/media/qrcode.3f4a3ef2.jpg) no-repeat center;
  background-size: 200px;
}
.page-login .form-box .login-form .download-box {
  text-align: center;
  font-size: 12px;
}
